@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  margin: 0;
  padding: 0;
  background-color: #789FCC;
  color: white;
}

p {
  font-family: "Roboto", sans-serif;
}

h1, h2, h3, h4 {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: "Montserrat", sans-serif;
}

.Menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px 5px;
}

#leftSideMenu {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0;
  margin: 0;
}

.interTitle {
  margin-top: 0;
  margin-bottom: 5px;
  margin-left: 5px;
}

.SplashScreen {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
}

.SplashScreen h2 {
  font-size: 4em;
  margin: 0;
  padding: 0;
}

.SplashScreen #currentIcon {
  width: 150px;
  animation: heartbeat 8s ease-in-out infinite both;
}

.SplashScreen .description {
  font-size: 1.3em;
}

.SplashScreen h3 {
  margin: 5px 0px;
}

.HourList {
  display: flex;
  flex-direction: row;
  overflow: scroll;
  padding-bottom: 10px;
}

.HourInfo {
  display: flex;
  flex-direction: column;
  background-color: #EDB97E;
  margin-left: 5px;
  padding: 20px 30px;
  text-align: center;
  border-radius: 2px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
}

.HourInfo p {
  font-size: 1.4em;
  margin: 0;
  padding: 0;
}

.HourInfo img {
  height: 50px;
}

.RainGraph {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  text-align: center;
  margin-top: 20px;
  height: 20vh;
  margin: 0px 5px;
  background-color: #EDB97E;
  overflow: scroll;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
}

.RainGraph .graphContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: 5px;
  height: 100%;
}

.RainGraph .bar {
  background-color: #789FCC;
  min-width: 20px;
  padding: 0;
  margin-left: 10px;
}

@keyframes heartbeat {
  from {
    transform: scale(1);
    transform-origin: center center;
    animation-timing-function: ease-out;
  }
  50% {
    transform: scale(0.9);
    animation-timing-function: ease-out;
  }
  100% {
    transform: scale(1);
    animation-timing-function: ease-out;
  }
}

@keyframes bobble {
  0% {
    transform: translate3d(30px, 40px, 30px);
    animation-timing-function: ease-in;
  }
  50% {
    transform: translate3d(30px, 60px, 40px);
    animation-timing-function: ease-out;
  }
  100% {
    transform: translate3d(30px, 40px, 30px);
  }
}

@keyframes leftToRight {
  0% {
    transform: translateX(-50vw);
  }
  100% {
    transform: translateX(150vw);
  }
}
